body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #000; /* Set background to black */
  color: #FFFFFF; /* Gold color for text */
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  width: 90%; /* Default to 100% for mobile */
  background-color: transparent;
}

@media (min-width: 768px) { /* Adjust this breakpoint as needed */
  .container {
    width: 40%; /* One third width for desktop */
    margin: 0 auto; /* Center the container */
  }
}

.image {
  max-width: 100%; /* Responsive image */
  height: auto; /* Maintain aspect ratio */
  margin: 20px 0;
}

.button-container {
  display: flex;
  flex-direction: column;
  gap: 10px; /* Space between buttons */
}

.button {
  background-color: #FFD700; /* Gold background for buttons */
  color: #000; /* Black text for contrast */
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  text-align: center;
  border: 2px solid #FFD700; /* Gold border */
}

.button:hover {
  background-color: #e6be00; /* Darker gold shade on hover */
  border-color: #e6be00; /* Change border color on hover */
}
.neon-text {
  color: #ffffff;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.7), 0 0 20px rgba(255, 165, 0, 0.8); /* Neon text effect */
}


.neon-border-text {
  font-size: 2em; /* Adjust size as needed */
  color: #FFD700; /* Gold color for the text */
  text-align: center; /* Center the text */
  padding: 20px; /* Add padding around the text */
  border: 5px solid transparent; /* Initial border */
  border-radius: 10px; /* Rounded corners */
  display: inline-block; /* Make the border only around the text */
  max-width: 100%; /* Max width of the body */
  box-shadow: 0 0 5px #FFD700,   /* Inner glow effect */
              0 0 10px #FFD700,
              0 0 15px #FFD700,
              0 0 20px #FFD700,
              0 0 30px #FF4500,  /* Outer glow */
              0 0 40px #FF4500; 
  background-color: rgba(0, 0, 0, 0.7); /* Slightly dark background for contrast */
}

.glow {
  border: 2px solid transparent; /* Transparent border */
  border-radius: 10px; /* Rounded corners */
  padding: 20px;
  text-align: center;
  box-shadow: 0 0 10px rgba(255, 215, 0, 0.8); /* Glowing effect */
  transition: box-shadow 0.3s ease-in-out; /* Smooth transition */
}

.glow:hover {
  box-shadow: 0 0 20px rgba(255, 215, 0, 1); /* Stronger glow on hover */
}


.full-width-border {
  width: 100%;
  border: 2px solid beige; /* Transparent border */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0 0 5px #FFD700,   /* Inner glow effect */
              
              0 0 30px #FFD700,  /* Outer glow */
              0 0 40px #FFD700; 
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 20px; /* Spacing between buttons */
  margin-top: 20px;
}

.social-button {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  color: white;
  border-radius: 30px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.social-button .fa-icon {
  margin-right: 10px;
}

.facebook {
  background-color: #3b5998; /* Facebook Blue */
}

.tiktok {
  background-color: #010101; /* TikTok Black */
}

.youtube {
  background-color: #FF0000; /* YouTube Red */
}

.social-button:hover {
  transform: scale(1.05); /* Slight zoom on hover */
}

.social-button.facebook:hover {
  background-color: #2d4373;
}

.social-button.tiktok:hover {
  background-color: #333;
}

.social-button.youtube:hover {
  background-color: #cc0000;
}
.app-download-button {
  display: inline-block;
  text-align: center;
  padding: 15px 30px;
  border-radius: 40px;
  background-color: #282828;
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0 0 15px rgba(255, 165, 0, 0.7), 0 0 30px rgba(255, 165, 0, 0.5); /* Neon glow */
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.app-download-button h2 {
  margin: 0;
}

.app-download-button:hover {
  background-color: #f39c12; /* Brighten the background on hover */
  transform: scale(1.05); /* Slight zoom on hover */
  box-shadow: 0 0 20px rgba(255, 165, 0, 0.8), 0 0 40px rgba(255, 165, 0, 0.6); /* Stronger neon glow on hover */
}

/* Popup Styles */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.popup-content {
  background: black;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}

.telegram-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  background-color: #0088cc;
  color: white;
  font-size: 1rem;
  border-radius: 5px;
  text-decoration: none;
}

.close-button {
  margin-top: 1rem;
  background-color: #ccc;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 5px;
}
